.swatchControler {

    display: flex;
    width: 136px;
    justify-content: space-between;
    align-items: center;


    &__box {
        position: relative;
        width: 24px;
        height: 24px;

        label {
            width: 24px;
            height: 24px;

            display: block;
            position: absolute;
            top: 0;
            left: 0;

            border: 1px solid rgb(77, 77, 77);
            filter: saturate(30%);
        }

        input {
            opacity: 0;
        }

        input:checked+label {
            border: 2px solid rgb(124, 124, 124);
            filter: none;

        }
    }
}